import { FilterOutlined, SearchOutlined } from "@ant-design/icons";
import { Col, Collapse, DatePicker, Divider, Form, message, Modal, Row, Select, Tooltip, Input } from "antd";
import SearchFilterDropdown from "components/shared-components/SearchFilter";
import TableComponent from "components/shared-components/TableComponent/Index";
import {
  ADD_LABEL,
  ALLOCATION_POPUP_TITLE,
  ALLOCATION_STATUS_TABLE_COLUMNS,
  FEEDBACK_TABLE_COLUMNS,
  AVAILABLE_FROM,
  CONFIRM_ALERT,
  FORM_FIELDS,
  RESERVATION_DETAIL,
  FEEDBACK_DETAIL,
} from "constants/ChangeAllocationStatusConstant";
import {
  CHILDREN,
  CONFIRM_MESSAGE,
  DATE_FORMAT,
  FORM_NAME,
  LIGHT_NAVY_BLUE_COLOR,
  SELECT_TEXT,
  SORT_DIRECTIONS,
  VALIDATION_FAILED,
} from "constants/CommonConstants";
import { EMPLOYEE_LABELS, ALLOCATION_STATUSES } from "constants/ManageEmployeeConstants";
import { SELECT_LAYOUT } from "constants/ManageProjectsConstant";
import useIntlMessage from "hooks/useIntlMessage";
import moment from "moment";
import { useCallback, useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setEmployeeDataByCountry } from "redux/actions";
import { EmployeeService } from "services";
import employeeService, { getFlatAllocations } from "services/EmployeeService";
import projectService from "services/ProjectService";
import Utils from "utils";

const AllocationStatusModal = props => {
  const { visible, onCancel, employee, onSave } = props;
  const { EMPLOYEEP_NAME, EMPLOYEE_ID, CURRENT_STATUS, PARENT_ACCOUNT } = EMPLOYEE_LABELS;
  const [allocationStatus, setAllocationStatus] = useState("");
  const [sortedInfo, setSortedInfo] = useState({});
  const [, setSearchText] = useState("");
  const [, setsearchedColumn] = useState("");
  const [employeeWithAllocations, setEmployeeWithAllocations] = useState(null);
  const [allocations, setAllocations] = useState([]);
  const [filteredInfo, setFilteredInfo] = useState({});
  const [currentPageSize, setCurrentPageSize] = useState(10);
  const [currentPage, setCurrentPage] = useState(1);
  const [loading, setLoading] = useState(false);
  const [selectedProject, setSelectedProject] = useState(null);
  const [projectList, setProjectList] = useState([]);
  const [accountList, setAccountList] = useState([]);
  const [hasChanges, setHasChanges] = useState(false);
  const [subDomainList, setSubDomainList] = useState([]);
  const allocationLevel = useSelector(state => state.myTeams.masterData.AssignedLevel);
  const subDomains = useSelector(state => state.myTeams.subdomains);
  const domainList = useSelector(state => state.myTeams.domains);
  const [isRequired, setIsRequired] = useState(false);
  const [showLeverURL, setShowLeverURL] = useState(false);
  const employeeDataByCountry = useSelector(state => state.myTeams.employeeDataByCountry);
  const [feedbackData, setFeedbackData] = useState([]);

  const dispatch = useDispatch();

  const [form] = Form.useForm();
  const formItemLayout = {
    labelCol: { span: 10 },
    wrapperCol: { span: 12 },
  };
  const { Option } = Select;
  const { TextArea } = Input;

  const internalBillable = "INTERNAL_BILLABLE";
  const { ACCOUNT, PROJECT, ALLOCATION_LEVEL, ALLOCATION_STATUS, START_DATE, DOMAIN_NAME, SUB_DOMAIN } = FORM_FIELDS;
  const { confirm } = Modal;

  let searchInput = useRef(null);
  const getMessage = useIntlMessage();

  const fetchInitialData = useCallback(async () => {
    try {
      setLoading(true);
      const accountPromise = projectService.getAccountListByEmpId();
      const employeeAllocationsPromise = employeeService.getAllocations(employee.employeeId);
      const allAllocationsFeedbackDataPromise = employeeService.getAllAllocationsFeedback(employee.employeeId);
      const [accountsList, employeeAllocations, allAllocationsFeedbackData] = await Promise.all([
        accountPromise,
        employeeAllocationsPromise,
        allAllocationsFeedbackDataPromise,
      ]);
      setAccountList(accountsList);
      setEmployeeWithAllocations(employeeAllocations);
      setFeedbackData(allAllocationsFeedbackData);
      const allocations = getFlatAllocations(employeeAllocations.reservations);
      setAllocations(allocations);
    } catch (error) {
      console.error("Something went wrong", error);
    } finally {
      setLoading(false);
    }
  }, [employee.employeeId]);

  useEffect(() => {
    fetchInitialData();
  }, [fetchInitialData]);

  const handleAccountChange = async accountId => {
    try {
      setSelectedProject(null);
      setSubDomainList([]);
      form.setFieldsValue({ [PROJECT.name]: null, [DOMAIN_NAME.name]: null, [SUB_DOMAIN.name]: null });
      const projectList = accountId && (await projectService.getListByAccountId(accountId));
      setProjectList(projectList);
    } catch (error) {
      console.error("Something went wrong while feching project list by account id", error);
    }
  };

  const handleSearch = (selectedKeys, confirm, dataIndex) => {
    confirm();
    setSearchText(selectedKeys[0]);
    setsearchedColumn(dataIndex);
  };

  const handleReset = (clearFilters, confirm) => {
    clearFilters?.();
    setSearchText("");
    confirm?.();
  };

  const getColumnSearchProps = field => {
    let filterObj = { filterIcon: filtered => <SearchOutlined color={filtered ? LIGHT_NAVY_BLUE_COLOR : undefined} /> };
    if (field.type === "select") {
      if (!Utils.isEmpty(field.options)) {
        filterObj.filters = field?.options?.map(option => ({
          text: getMessage(option?.label),
          value: option.value,
        }));
      }
      filterObj.filterIcon = filtered => <FilterOutlined color={filtered ? LIGHT_NAVY_BLUE_COLOR : undefined} />;
      filterObj.onFilter = (value, record) => record[field.key] === value;
    } else {
      filterObj.filterDropdown = ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
        <SearchFilterDropdown
          setSelectedKeys={setSelectedKeys}
          selectedKeys={selectedKeys}
          confirm={confirm}
          clearFilters={clearFilters}
          handleSearch={handleSearch}
          dataIndex={field.key}
          searchInput={searchInput}
          handleReset={handleReset}
          label={getMessage(field.label)}
        />
      );

      filterObj.onFilter = (value, record) => {
        if (field.type === "date") {
          const valuee = value.format(DATE_FORMAT).toString();
          const timeStampToDate = moment(record[field.key])?.format(DATE_FORMAT)?.toString();
          return timeStampToDate.toLowerCase().includes(valuee.toLowerCase());
        }
        return record[field.key]?.toString()?.toLowerCase().includes(value.toLowerCase());
      };
      filterObj.onFilterDropdownVisibleChange = visible => {
        if (visible) {
          setTimeout(() => searchInput && searchInput.select && searchInput.select());
        }
      };
    }
    return filterObj;
  };

  const handleChange = (pagination, filters, sorter) => {
    setFilteredInfo(filters);
    setSortedInfo(sorter);
    const { current, pageSize } = pagination;
    setCurrentPageSize(pageSize);
    setCurrentPage(current);
  };
  const generateColumns = tableColumns => {
    return Object.values(tableColumns).map(field => ({
      title: (
        <Tooltip title={getMessage(field.label)} placement="top">
          <span>{getMessage(field.label)}</span>
        </Tooltip>
      ),
      dataIndex: field.key,
      key: field.key,
      sorter: (a, b) => Utils.antdTableSorter(a, b, field.key),
      sortDirections: SORT_DIRECTIONS,
      filteredValue: filteredInfo[field.key] || null,
      sortOrder: sortedInfo.columnKey === field.key ? sortedInfo.order : null,
      ...getColumnSearchProps(field),
      render: (_, record) => {
        if (field.type === "date") {
          return record[field.key] && moment(record[field.key]).format(DATE_FORMAT);
        }
        return record[field.key];
      },
    }));
  };

  const dataColumns = generateColumns(ALLOCATION_STATUS_TABLE_COLUMNS);
  const feedBackColumns = generateColumns(FEEDBACK_TABLE_COLUMNS);
  const handleAllocationStatus = status => {
    setAllocationStatus(status);
    setShowLeverURL(status.toLowerCase() === "reserved");
    form.setFieldsValue({ [START_DATE.name]: null });
  };

  const disabledDate = useCallback(
    function disabledDate(current) {
      if (!allocationStatus) return;
      const numberOfBackWeeks = allocationStatus.toLowerCase() === "reserved" ? 2 : 4;
      const availableWeeks = moment().diff(employeeWithAllocations?.allowedAllocationDate, "weeks");
      const future = moment().subtract(0, "days");
      if (availableWeeks >= 4) {
        const twoWeeksBack = moment().subtract(numberOfBackWeeks, "weeks");
        return current < twoWeeksBack || current > future;
      } else if (availableWeeks >= 2 && numberOfBackWeeks === 2) {
        const twoWeeksBack = moment().subtract(2, "weeks");
        return current < twoWeeksBack || current > future;
      } else {
        // Get days from the date.
        const availableDays = moment().diff(employeeWithAllocations?.allowedAllocationDate, "days");
        return moment().add(-Math.abs(availableDays + 1), "days") >= current || current > future;
      }
    },
    [allocationStatus],
  );

  const handleCancel = () => {
    if (hasChanges) {
      confirm({
        title: CONFIRM_MESSAGE.value,
        centered: true,
        onOk() {
          onCancel();
        },
      });
    } else {
      form.resetFields();
      onCancel();
    }
  };

  const handleAddAllocation = async () => {
    try {
      setLoading(true);
      let formValues = await form.validateFields();
      confirm({
        title: getMessage(CONFIRM_ALERT),
        centered: true,
        onOk: async () => {
          try {
            const response = await employeeService.addAllocation({ ...formValues, empId: employee.employeeId });
            if (response.statusCode === 200) {
              form.resetFields();
              message.success(response?.message);
              setAllocationStatus(null);
              setLoading(true);
              updateEmpStatusOnRedux(response, employee.employeeId);
            } else {
              message.error(response?.message);
            }
          } catch (error) {
            console.error(error);
          }
        },
      });
    } catch (info) {
      console.error(VALIDATION_FAILED.value, info);
    } finally {
      setLoading(false);
    }
  };

  const updateEmpStatusOnRedux = (resp, empId) => {
    const employeeData = [...employeeDataByCountry];
    const indexOfEmployee = employeeData.findIndex(x => x.employeeId === empId);
    const findEmp = { ...employeeData.find(x => x.employeeId === empId) };
    if (findEmp) {
      findEmp.employeeAllocationDataDTO.allocationStatus = resp?.records?.allocationStatus;
      findEmp["isReservable"] = resp?.records?.isReservable;
      findEmp.employeeAllocationDataDTO["reservedFor"] = resp?.records?.reservedFor;
      findEmp.employeeAllocationDataDTO["parentAccount"] = resp?.records?.parentAccount;
      employeeData.splice(indexOfEmployee, 1, findEmp);
      const data = EmployeeService.prepareEmployeeList(employeeData);
      dispatch(setEmployeeDataByCountry(data));
      onSave();
    }
  };

  const handleDomainChange = domainId => {
    setSelectedProject(true);
    setSubDomainList([]);
    const subDomainsLists = subDomains.filter(subDomain => subDomain.domainId === domainId);
    setSubDomainList(subDomainsLists);
    form.setFieldsValue({ [SUB_DOMAIN.name]: null });
  };

  const validateFieldRequired = () => {
    const isInternalBillable = allocationStatus === internalBillable;
    if (
      isInternalBillable ||
      form.getFieldValue(PROJECT.name) ||
      form.getFieldValue(DOMAIN_NAME.name) ||
      form.getFieldValue(SUB_DOMAIN.name)
    ) {
      setIsRequired(true);
    } else {
      setIsRequired(false);
      form.resetFields([PROJECT.name, DOMAIN_NAME.name, SUB_DOMAIN.name]);
      form.setFieldsValue({
        [PROJECT.name]: null,
        [DOMAIN_NAME.name]: null,
        [SUB_DOMAIN.name]: null,
      });
    }
  };

  const isAllocationStatusReserved = employeeWithAllocations?.currentAllocationStatus === ALLOCATION_STATUSES[0].value;

  const allocationStatusOptions = isAllocationStatusReserved ? [ALLOCATION_STATUSES[0]] : ALLOCATION_STATUSES;

  return (
    <Modal
      okText={getMessage(ADD_LABEL)}
      width={"85%"}
      visible={visible}
      onOk={handleAddAllocation}
      onCancel={handleCancel}
      title={getMessage(ALLOCATION_POPUP_TITLE)}
    >
      <Row className="mb-2" gutter={12}>
        <Col xs={24} md={6}>
          <strong>{getMessage(EMPLOYEE_ID)}:</strong> {employee.employeeId}
        </Col>
        <Col xs={24} md={8}>
          <strong>{getMessage(EMPLOYEEP_NAME)}:</strong> {employee.employeeName}
        </Col>
        <Col xs={24} md={8}>
          <strong>{getMessage(PARENT_ACCOUNT)}:</strong> {employeeWithAllocations?.parentAccount?.accountName}
        </Col>
      </Row>
      <Row gutter={12}>
        <Col xs={24} md={6}>
          <strong>{getMessage(CURRENT_STATUS)}: </strong> {employeeWithAllocations?.currentAllocationStatus}
        </Col>
        <Col xs={24} md={6}>
          <strong>{getMessage(AVAILABLE_FROM)}: </strong>
          {moment(employeeWithAllocations?.allowedAllocationDate).format(DATE_FORMAT)}
        </Col>
      </Row>
      <Divider />
      <br />

      <Form
        form={form}
        name={FORM_NAME.text}
        layout={SELECT_LAYOUT.text}
        onFieldsChange={() => {
          setHasChanges(true);
          validateFieldRequired();
        }}
        {...formItemLayout}
      >
        <Row gutter={12}>
          <Col xs={24} md={12} lg={12}>
            <Form.Item
              labelAlign="left"
              label={getMessage(ALLOCATION_STATUS.label)}
              name={ALLOCATION_STATUS.name}
              rules={[
                {
                  required: true,
                },
              ]}
            >
              <Select
                onChange={handleAllocationStatus}
                placeholder={getMessage(SELECT_TEXT.label, ALLOCATION_STATUS.label)}
                allowClear
              >
                {allocationStatusOptions?.map(e => {
                  return (
                    <Option key={e.value} value={e.value}>
                      {getMessage(e.label)}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>
          {/* <Col xs={24} md={12} lg={12}>
            <Form.Item
              labelAlign="left"
              name={ALLOCATION_LEVEL.name}
              label={getMessage(ALLOCATION_LEVEL.label)}
              rules={[
                {
                  required: true,
                },
              ]}
            >
              <Select placeholder={getMessage(SELECT_TEXT.label, ALLOCATION_LEVEL.label)} allowClear>
                {Utils.sortArray(allocationLevel)?.map(e => {
                  return (
                    <Option key={e} value={e}>
                      {e}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col> */}

          <Col xs={24} md={12} lg={12}>
            <Form.Item
              name={ACCOUNT?.name}
              label={getMessage(ACCOUNT?.label)}
              rules={[
                {
                  required: true,
                },
              ]}
              labelAlign="left"
            >
              <Select
                showArrow
                showSearch
                allowClear
                optionFilterProp={CHILDREN}
                placeholder={getMessage(SELECT_TEXT.label, ACCOUNT?.label)}
                onChange={handleAccountChange}
              >
                {accountList?.map(e => {
                  return (
                    <Option key={e.accountId} value={e.accountId}>
                      {e.accountName}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>
          <Col xs={24} md={12} lg={12}>
            <Form.Item
              name={PROJECT.name}
              label={getMessage(PROJECT.label)}
              labelAlign="left"
              rules={[{ required: isRequired }]}
            >
              <Select
                showArrow
                showSearch
                allowClear
                optionFilterProp={CHILDREN}
                placeholder={getMessage(SELECT_TEXT.label, PROJECT.label)}
                // onChange={setSelectedProject}
              >
                {Utils.sortArray(projectList, PROJECT.name)?.map(e => {
                  return (
                    <Option key={e?.projectId} value={e?.projectId}>
                      {e?.projectName}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>

          <Col xs={24} md={12} lg={12}>
            <Form.Item
              name={DOMAIN_NAME.name}
              label={getMessage(DOMAIN_NAME.label)}
              labelAlign="left"
              rules={[{ required: isRequired }]}
            >
              <Select
                showArrow
                showSearch
                allowClear
                optionFilterProp={CHILDREN}
                placeholder={getMessage(SELECT_TEXT.label, DOMAIN_NAME.label)}
                onChange={handleDomainChange}
              >
                {Utils.sortArray(domainList, DOMAIN_NAME.name)?.map(e => {
                  return (
                    <Option key={e.domainId} value={e.domainId}>
                      {e.domainName}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>
          <Col xs={24} md={12} lg={12}>
            <Form.Item
              name={SUB_DOMAIN.name}
              labelAlign="left"
              label={getMessage(SUB_DOMAIN.label)}
              rules={[{ required: isRequired }]}
            >
              <Select placeholder={getMessage(SELECT_TEXT.label, SUB_DOMAIN.label)} allowClear>
                {Utils.sortArray(subDomainList, "subDomainName")?.map(e => {
                  return (
                    <Option key={e.subDomainId} value={e.subDomainId}>
                      {e.subDomainName}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>

          <Col xs={24} md={12} lg={12}>
            <Form.Item
              labelAlign="left"
              label={getMessage(START_DATE.label)}
              name={START_DATE.name}
              rules={[
                {
                  required: true,
                },
              ]}
            >
              {!allocationStatus ? null : (
                <DatePicker
                  className="w-100"
                  //  disabled={!allocationStatus}
                  disabledDate={disabledDate}
                  // disabledDate={(current) => current.isBefore(moment().subtract(1,"day"))}
                  format={DATE_FORMAT}
                 
                />
              )}
            </Form.Item>
          </Col>
          <Col xs={24} md={12} lg={12} xl={12}>
            <Form.Item labelAlign="left" name={FORM_FIELDS.NOTES.name} label={getMessage(FORM_FIELDS.NOTES.label)}>
              <TextArea rows={2} maxLength={500} placeholder={getMessage(FORM_FIELDS.NOTES.label)} />
            </Form.Item>
          </Col>
          {showLeverURL ? (
            <Col xs={24} md={12} lg={12} xl={12}>
              <Form.Item
                labelAlign="left"
                name={FORM_FIELDS.LEVER.name}
                label={getMessage(FORM_FIELDS.LEVER.label)}
                rules={[
                  {
                    pattern: new RegExp(/^(ftp|http|https):\/\/[^ "]+$/i),
                    message: "Only valid URL is Allowed",
                  },
                ]}
              >
                <Input />
              </Form.Item>
            </Col>
          ) : null}
        </Row>
      </Form>
      <br />
      <Collapse bordered={false}>
        <Collapse.Panel header={getMessage(FEEDBACK_DETAIL)}>
          <TableComponent
            paginationHandler={{
              defaultPageSize: currentPageSize,
              current: currentPage,
            }}
            tableSize="small"
            handleChange={handleChange}
            columns={feedBackColumns}
            data={feedbackData}
            loading={loading}
          />
        </Collapse.Panel>
      </Collapse>
      <br />
      <Collapse bordered={false}>
        <Collapse.Panel header={getMessage(RESERVATION_DETAIL)}>
          <TableComponent
            paginationHandler={{
              defaultPageSize: currentPageSize,
              current: currentPage,
            }}
            tableSize="small"
            handleChange={handleChange}
            columns={dataColumns}
            data={allocations}
            loading={loading}
          />
        </Collapse.Panel>
      </Collapse>
    </Modal>
  );
};

export default AllocationStatusModal;
