import {
  DiffOutlined,
  DownloadOutlined,
  EditOutlined,
  FilterOutlined,
  PlusCircleOutlined,
  SearchOutlined,
  SyncOutlined,
  PlusCircleFilled,
} from "@ant-design/icons";
import { Button, Col, Form, Grid, PageHeader, Radio, Row, Select, Tooltip } from "antd";
import EmployeeSample from "assets/data/EmployeeSamplebulk.xlsx";
import Flex from "components/shared-components/Flex";
import SearchFilterDropdown from "components/shared-components/SearchFilter";
import TableComponent from "components/shared-components/TableComponent/Index";
import RestrictedContainer from "components/util-components/RestrictedContainer";
import {
  ACTIONS,
  ACTIVE_STATUS,
  CHILDREN,
  DATE_FORMAT,
  LIGHT_NAVY_BLUE_COLOR,
  NAVIGATION_ASSETS,
  NAVIGATION_ASSETS_RIGHTS,
  SCREEN_ITEMS_TYPES,
  SORT_DIRECTIONS,
} from "constants/CommonConstants";
import { ADMIN_ROLES, EMPLOYEE_TABLE, ROLES, STATUSES } from "constants/ManageEmployeeConstants";
import useIntlMessage from "hooks/useIntlMessage";
import moment from "moment";
import { useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { showLoader } from "redux/actions/Loader";
import employeeService from "services/EmployeeService";
import Utils from "utils";
import uploadDataMethods from "utils/UploadDataFunctions";
import {
  SetCountryForEmployee,
  setEmployeeDataByCountry,
  setStatusForEmployee,
} from "../../../../../redux/actions/MyTeams";
import AddEmployeeForms from "./AddEmployeeForms";
import AllocationStatusModal from "./AllocationStatusModal";

import UploadData from "../../../../../components/UploadData/index";
import BulkUploadEmployee from "./BulkUploadEmployee";
import RehireForm from "./RehireForm";

const { useBreakpoint } = Grid;

const ManageEmployees = () => {
  const dispatch = useDispatch();
  const [editEmployee, setEditEmployee] = useState(null);
  const [employeeStatus, setEmployeeStatus] = useState(ACTIVE_STATUS.value);
  const [filteredUsersBasedOnStatus, setFilteredUsersBasedOnStatus] = useState([]);
  const [searchText, setSearchText] = useState("");
  const [searchedColumn, setsearchedColumn] = useState("");
  const [filteredInfo, setFilteredInfo] = useState({});
  const [isLoading, setIsLoading] = useState(false);
  const [sortedInfo, setSortedInfo] = useState({});
  const [modalVisible, setVisible] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [showAllocationStatusModal, setShowAllocationStatusModal] = useState(false);
  // const employeeData = useSelector(state => state.myTeams.employeeData);
  const [selectedEmployee, setSelectedEmployee] = useState(null);
  const [currentPageSize, setCurrentPageSize] = useState(10);
  const [selectedCountry, setSelectedCountry] = useState(null);
  const isMobile = !Utils.getBreakPoint(useBreakpoint()).includes("sm");
  const [tableKey, setTableKey] = useState(0);
  const role = useSelector(state => state?.myTeams?.userInfo?.role);
  const locations = useSelector(state => state.myTeams.allLocations);
  const previousCountrySelected = useSelector(state => state.myTeams.countryForEmployee);
  const employeeDataByCountry = useSelector(state => state.myTeams.employeeDataByCountry);
  const prevStatusForEmployee = useSelector(state => state.myTeams.statusForEmployee);
  const [showUploadModal, setShowUplaodModal] = useState(false);
  let searchInput = useRef(null);
  const getMessage = useIntlMessage();
  const { Option } = Select;
  const [form] = Form.useForm();
  const [isFirstLoad, setIsFirstLoad] = useState(true);
  const [stateUpdate, setStateUpdate] = useState(false);
  const [bulkResponse, setBulkResponse] = useState(null);
  const [showBulkUpload, setShowBulkUpload] = useState(false);

  const [rehireShow, setRehireShow] = useState(false);
  const [rehireEmployee, setRehireEmployee] = useState(null);

  useEffect(() => {
    if (selectedCountry) {
      dispatch(SetCountryForEmployee(selectedCountry));
      handleOnSearch();
      setStateUpdate(true);
    }
  }, [selectedCountry]);

  useEffect(() => {
    if (stateUpdate) {
      setFilteredUsersBasedOnStatus(employeeDataByCountry);
      setStateUpdate(false);
    }
  }, [stateUpdate]);

  const handleFetchEmployees = () => {
    setStateUpdate(true);
    setSearchText("");
    setsearchedColumn("");
    setFilteredUsersBasedOnStatus(employeeDataByCountry);
    setFilteredInfo({});
    setSortedInfo({});
    handleReset();
    setCurrentPage(1);
  };

  const handleFiltersNotRemove = () => {
    setStateUpdate(true);
    setFilteredUsersBasedOnStatus(employeeDataByCountry);
  };

  const handleOnChangeStatus = e => {
    const { value: status } = e.target;
    setEmployeeStatus(status);
    form
      .validateFields()
      .then(() => {
        if (status === prevStatusForEmployee && isFirstLoad) {
          setFilteredUsersBasedOnStatus(employeeDataByCountry);
        } else {
          getEmployeeList(status, selectedCountry);
          dispatch(setStatusForEmployee(status));
        }
      })
      .catch(e => {
        console.error(e);
      });
  };

  const showModal = () => {
    setVisible(true);
  };

  const handleUploadModal = () => {
    setShowUplaodModal(true);
  };

  const handleOk = e => {
    setVisible(false);
  };

  const handleCancel = e => {
    setVisible(false);
    setEditEmployee(null);
  };

  const handleUploadCancel = e => {
    setShowUplaodModal(false);
  };

  const handleCancelAllocationStatuModal = () => {
    setShowAllocationStatusModal(false);
  };
  const onSave = () => {
    setFilteredUsersBasedOnStatus(employeeDataByCountry);
    setStateUpdate(true);
    setShowAllocationStatusModal(false);
  };

  const getColumnSearchProps = field => {
    let filterObj = {
      filterIcon: filtered => (
        <SearchOutlined
          color={filtered ? LIGHT_NAVY_BLUE_COLOR : undefined}
          id={`manage-emp-search-btn-${field.key}`}
        />
      ),
    };
    if (field.type === "select") {
      if (!Utils.isEmpty(field.options)) {
        filterObj.filters = field.options.map(option => ({
          text: getMessage(option.label),
          value: option.value,
        }));
      }
      filterObj.filterIcon = filtered => (
        <FilterOutlined
          color={filtered ? LIGHT_NAVY_BLUE_COLOR : undefined}
          id={`manage-emp-search-btn-${field.key}`}
        />
      );
      filterObj.onFilter = (value, record) => record[field.key] === value;
    } else {
      filterObj.filterDropdown = ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
        <SearchFilterDropdown
          setSelectedKeys={setSelectedKeys}
          selectedKeys={selectedKeys}
          confirm={confirm}
          clearFilters={clearFilters}
          handleSearch={handleSearch}
          dataIndex={field.key}
          field={field}
          searchInput={searchInput}
          handleReset={handleReset}
          label={getMessage(field.label)}
          inputId={`manage-emp-input-${field.key}`}
          resetBtnId={`manage-emp-reset-btn-${field.key}`}
          searchBtnId={`manage-emp-search-btn-${field.key}`}
          dataSource={filteredUsersBasedOnStatus}
        />
      );

      filterObj.onFilter = (value, record) => {
        if (field.type === "date") {
          const valuee = value.format(DATE_FORMAT).toString();
          const timeStampToDate = moment(record[field.key])?.format(DATE_FORMAT)?.toString();
          return timeStampToDate.toLowerCase().includes(valuee.toLowerCase());
        }
        return record[field.key]?.toString()?.toLowerCase().includes(value.toLowerCase());
      };
      filterObj.onFilterDropdownVisibleChange = visible => {
        if (visible) {
          setTimeout(() => searchInput && searchInput.select && searchInput.select());
        }
      };
    }
    return filterObj;
  };

  const handleChange = (pagination, filters, sorter) => {
    setFilteredInfo(filters);
    setSortedInfo(sorter);
    const { current, pageSize } = pagination;
    setCurrentPageSize(pageSize);
    setCurrentPage(current);
  };

  const handleSearch = (selectedKeys, confirm, dataIndex) => {
    confirm();
    setSearchText(selectedKeys[0]);
    setsearchedColumn(dataIndex);
  };

  const handleReset = (clearFilters, confirm) => {
    setTableKey(tableKey => tableKey + 1);
    clearFilters?.();
    setSearchText("");
    confirm?.();
  };

  const handleEditEmployee = record => {
    setEditEmployee(record);
    setVisible(true);
  };

  const handleToggleAllocationStatusModal = employee => {
    setShowAllocationStatusModal(!showAllocationStatusModal);
    setSelectedEmployee(employee);
  };
  const showRehireModal = employee => {
    setRehireEmployee(employee);
    setRehireShow(true);
  };
  const hideRehireModal = () => {
    setRehireShow(false);
  };
  const dataColumns = Object.values(EMPLOYEE_TABLE)
    .map(field => ({
      title: (
        <Tooltip title={getMessage(field.label)} placement="top">
          <span>{getMessage(field.label)}</span>
        </Tooltip>
      ),
      dataIndex: field.key,
      key: field.key,
      sorter: (a, b) => Utils.antdTableSorter(a, b, field.key),
      sortDirections: SORT_DIRECTIONS,
      filteredValue: filteredInfo[field.key] || null,
      ellipsis: true,
      width: field.width || null,
      sortOrder: sortedInfo.columnKey === field.key ? sortedInfo.order : null,
      ...getColumnSearchProps(field),
      render: (_, record) => {
        if (field.type === "date") {
          return record[field.key] ? moment(record[field.key]).format(DATE_FORMAT) : "";
        }
        if (field.key === EMPLOYEE_TABLE.EMP_SKILLS.key) {
          return (
            <span
              style={{
                width: 120,
                display: "inline-block",
                whiteSpace: "nowrap",
                overflow: "hidden",
                textOverflow: "ellipsis",
              }}
            >
              <Tooltip title={record[field.key]} placement="top">
                <span>{record[field.key]}</span>
              </Tooltip>
            </span>
          );
        }
        return (
          <Tooltip title={record[field.key]} placement="top">
            <span>{record[field.key]}</span>
          </Tooltip>
        );
      },
    }))
    .filter(col => {
      if (employeeStatus !== STATUSES.INACTIVE_STATUS.value && col.dataIndex === "endDate") return;
      return col;
    });

  const manageEmployeeColumns = [
    ...dataColumns,
    {
      title: ACTIONS.text,
      dataIndex: ACTIONS.value,
      key: ACTIONS.value,
      width: ROLES.includes(role) ? 160 : 120,
      fixed: isMobile ? "" : "right",
      render: (_, record) => {
        if (record.empStatus === ACTIVE_STATUS.value) {
          return (
            <Flex className="mr-2">
              <RestrictedContainer
                assetId={NAVIGATION_ASSETS.ManageEmployee}
                assetRight={NAVIGATION_ASSETS_RIGHTS.UPDATE}
              >
                <Tooltip title={getMessage("common.label.edit")}>
                  <Button onClick={() => handleEditEmployee(record)} size="small" id="add-employee">
                    <EditOutlined />
                  </Button>
                </Tooltip>
              </RestrictedContainer>

              {/* {!Utils.isEmpty(role) && ROLES.includes(role) ? (
                <Tooltip title={getMessage("component.manageemployees.download")}>
                  <Button
                    className="ml-1"
                    size="small"
                    id="manage-emp-download"
                    onClick={() => handleDownloadDocument(record)}
                  >
                    <DownloadOutlined />
                  </Button>
                </Tooltip>
              ) : null} */}
              {record.isReservable &&
              role !== ADMIN_ROLES.ADMIN.key &&
              role !== "RMG" &&
              record.functionalGroup !== "Practice Group" ? (
                <Tooltip title={getMessage("component.manageemployees.change.allocation")}>
                  <Button
                    size="small"
                    onClick={() => handleToggleAllocationStatusModal(record)}
                    className="ml-2"
                    id="manage-emp-allocation"
                  >
                    <DiffOutlined />
                  </Button>
                </Tooltip>
              ) : null}
            </Flex>
          );
        } else {
          return (
            record?.exitType === "TEMPORARY_EXIT" && (
              <Tooltip title="Activate ">
                <Button size="small" onClick={() => showRehireModal(record)} className="ml-2" id="manage-emp-rehire">
                  <PlusCircleFilled />
                </Button>
              </Tooltip>
            )
          );
        }
      },
    },
  ];

  const handleOnSearch = () => {
    form
      .validateFields()
      .then(() => {
        if (isFirstLoad && selectedCountry === previousCountrySelected && employeeStatus === prevStatusForEmployee) {
          setFilteredUsersBasedOnStatus(employeeDataByCountry);
        } else {
          getEmployeeList(employeeStatus, selectedCountry);
          dispatch(setStatusForEmployee(employeeStatus));
        }
      })
      .catch(e => {
        console.error(e);
      });
  };
  // const handleFileUpload = async info => {
  //   try {
  //     dispatch(showLoader(true));
  //     let formData = new FormData();
  //     formData.append("empId", getEmployeeIdFromStore());
  //     formData.append("file", info.file);
  //     const response = await employeeService.uploadDocument(formData);
  //     if (response?.statusCode === 200) {
  //       dispatch(showLoader(false));
  //       message.success(response);
  //     } else {
  //       dispatch(showLoader(false));
  //       message.error(response);
  //     }
  //   } catch (error) {
  //     console.error(error);
  //   }
  // };
  const getEmployeeList = async (status, country) => {
    try {
      dispatch(showLoader(true));
      setIsFirstLoad(false);
      const resp = await employeeService.getList({ status, country });
      const employeeList = employeeService.prepareEmployeeList(resp);
      if (employeeList) dispatch(setEmployeeDataByCountry(employeeList));
      setFilteredUsersBasedOnStatus(employeeList);
    } catch (e) {
      console.error("Error in getting the employees information", e);
    } finally {
      dispatch(showLoader(false));
    }
  };
  const handleDownloadDocument = async record => {
    await uploadDataMethods.downloadEmployeeResume(record.employeeId);
  };

  const onEmployeeResponse = response => {
    if (response) {
      setBulkResponse(response);
      setShowBulkUpload(true);
    }
  };
  const hideShowBulkUploadModal = () => {
    setShowBulkUpload(false);
  };
  const handleBulkRespone = resp => {
    return resp;
  };
  return (
    <>
      <Row align="middle" gutter={[20, 20]} className="mb-2">
        <Col xs={24} md={12} lg={12} xl={12}>
          <PageHeader
            className="p-2 pl-2"
            subTitle={
              <Button
                onClick={handleFetchEmployees}
                className="ml-4"
                id="emp-refresh-table-btn"
                disabled={!selectedCountry}
              >
                <SyncOutlined shape="circle" />
              </Button>
            }
            title={getMessage("component.manageemployees.heading")}
          />
        </Col>
        <Col xs={24} md={12} lg={12} xl={12} className=" text-right text-md-right text-lg-right text-lg-right pr-2 ">
          <RestrictedContainer assetId={NAVIGATION_ASSETS.ManageEmployee} assetRight={NAVIGATION_ASSETS_RIGHTS.CREATE}>
            <Button type="primary" onClick={showModal} id="manage-emp-addbtn" className="mr-2">
              <PlusCircleOutlined className="mr-2" id="manage-emp-addbtn-icon" />
              {getMessage("component.manageemployees.addemployee")}
            </Button>
          </RestrictedContainer>
          <RestrictedContainer assetId={NAVIGATION_ASSETS.ManageEmployee} assetRight={NAVIGATION_ASSETS_RIGHTS.CREATE}>
            <Button type="primary" onClick={handleUploadModal} id="manage-emp-upload-btn" className="mr-2">
              <PlusCircleOutlined className="mr-2" id="manage-emp-upload-btn-icon" />
              {getMessage("components.uploadEmployee.button")}
            </Button>
          </RestrictedContainer>
        </Col>
      </Row>

      <Row align="middle" gutter={[20, 20]} className="mb-2" justify="end">
        <Col xs={24} md={12} lg={6} xl={6}>
          <Form name="basic" form={form}>
            <Form.Item
              name="manage-project-select-account"
              label={getMessage("common.label.select-country")}
              rules={[{ required: true, message: "Select Country" }]}
              className="mb-0"
            >
              <Select
                className="w-100"
                placeholder={getMessage("common.label.select-country")}
                name={"manage-emp-select-country"}
                id="manage-emp-select-country"
                onChange={setSelectedCountry}
                showArrow
                showSearch
                optionFilterProp={CHILDREN}
              >
                {locations?.map(account => {
                  return (
                    <Option key={account?.country} value={account?.country}>
                      {account?.country}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Form>
        </Col>

        {!Utils.isEmpty(role) && ROLES.includes(role) ? (
          <Col xs={24} md={12} lg={6} xl={6} className="pr-0 text-left text-md-right text-lg-right">
            <Radio.Group onChange={handleOnChangeStatus} value={employeeStatus} id={"manage-emp-radio-btn-group"}>
              {Object.values(STATUSES).map(status => {
                if (status.value !== STATUSES.ALL_STATUS.value)
                  return (
                    <Radio value={status.value} id={`manage-emp-radio-btn-${status.value?.replace(/\s+/g, "")}`}>
                      {getMessage(status.label)}
                    </Radio>
                  );
              })}
            </Radio.Group>
          </Col>
        ) : null}
      </Row>

      <TableComponent
        tableKey={tableKey}
        rowKey={record => record.id?.timeStamp}
        columns={manageEmployeeColumns}
        data={filteredUsersBasedOnStatus}
        scroll={{ x: 1500 }}
        handleChange={handleChange}
        tableSize="small"
        showDropdown={false}
        loading={isLoading}
        paginationHandler={{
          defaultPageSize: currentPageSize,
          current: currentPage,
        }}
        showSorterTooltip={false}
      />
      {modalVisible && (
        <AddEmployeeForms
          employeeToEdit={editEmployee}
          handleOk={handleOk}
          handleCancel={handleCancel}
          visible={modalVisible}
          handleRefresh={() => {
            if (selectedCountry) {
              //    handleFiltersNotRemove();
              getEmployeeList("Active", selectedCountry);
            }
          }}
        />
      )}

      {showUploadModal && (
        <UploadData
          visible={showUploadModal}
          onCancel={handleUploadCancel}
          screen={SCREEN_ITEMS_TYPES.employee}
          sampleFile={EmployeeSample}
          onEmployeeResponse={onEmployeeResponse}
        />
      )}
      {showAllocationStatusModal && (
        <AllocationStatusModal
          employee={selectedEmployee}
          onCancel={handleCancelAllocationStatuModal}
          visible={showAllocationStatusModal}
          onSave={onSave}
        />
      )}
      {showBulkUpload && (
        <BulkUploadEmployee
          response={bulkResponse}
          visible={showBulkUpload}
          onCancel={hideShowBulkUploadModal}
          handleRefresh={() => {
            if (selectedCountry) {
              //    handleFiltersNotRemove();
              getEmployeeList("Active", selectedCountry);
            }
          }}
        />
      )}
      {rehireShow && (
        <RehireForm
          rehireEmployee={rehireEmployee}
          visible={rehireShow}
          onCancel={hideRehireModal}
          handleRefresh={() => handleOnChangeStatus({ target: { value: "Active" } })}
        />
      )}
    </>
  );
};

export default ManageEmployees;
